@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
html,
body {
  width: 100%;
  height: 100%;
}

body,
div,
section,
article,
p,
ul,
li,
a {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-size: 16px;
  font-family: "Nanum Gothic", sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: #333;
}

li {
  list-style: none;
}

header nav{display:table;table-layout:fixed;width:70%;position:fixed;right:0;z-index:9999}header .gnb{text-align:center;padding:1em 0;display:table-cell;color:#333;text-decoration:none;font-size:1.1rem}header .gnb:hover,header .gnb.active{font-weight:bold}@media screen and (max-width: 48rem){header nav{width:100%;bottom:0;background-color:#fcfcfc;border-top:1px solid #ddd}header .gnb{padding:0.8em 0;border-left:1px solid #ddd;font-size:0.9rem;letter-spacing:-0.5px}header .gnb:first-child{border-left:none}}

.scrollTop{position:fixed;bottom:2rem;right:0;cursor:pointer;transition:all ease 0.4s;opacity:0.5}.scrollTop:hover{opacity:1}@media screen and (max-width: 48rem){.scrollTop{bottom:5rem}}

.wrap{width:100%;height:100%}.wrap .about_title{display:flex;justify-content:center;width:100%;height:100%}.wrap #title{margin:1em;width:30em}.wrap .alpha{opacity:0;-webkit-animation:slide 2s ease forwards;animation:slide 2s ease forwards}.wrap .alpha_am{-webkit-animation-delay:0.7s;animation-delay:0.7s}.wrap .alpha_sohee{-webkit-animation-delay:2s;animation-delay:2s}.wrap .stroke{stroke-dasharray:500px;stroke-dashoffset:500px;-webkit-animation:stroke-move 30s ease forwards;animation:stroke-move 30s ease forwards}.wrap .stroke_two{stroke-dasharray:900px;stroke-dashoffset:900px;-webkit-animation:stroke-move2 30s ease forwards;animation:stroke-move2 30s ease forwards;-webkit-animation-delay:1.2s;animation-delay:1.2s}.wrap .stroke_three{-webkit-animation-delay:2.2s;animation-delay:2.2s}.wrap .about_intro{display:flex;width:100%;height:100%}.wrap .about_intro article{flex:1 1;padding:5em 1em}.wrap .about_intro article h2{margin:0.5em 0}@-webkit-keyframes slide{0%{opacity:0;transform:translateY(-5px)}100%{opacity:1;transform:translateY(0px)}}@keyframes slide{0%{opacity:0;transform:translateY(-5px)}100%{opacity:1;transform:translateY(0px)}}@-webkit-keyframes stroke-move{0%{stroke-dashoffset:500px}5%{stroke-dashoffset:0px}100%{stroke-dashoffset:0px}}@keyframes stroke-move{0%{stroke-dashoffset:500px}5%{stroke-dashoffset:0px}100%{stroke-dashoffset:0px}}@-webkit-keyframes stroke-move2{0%{stroke-dashoffset:900px}5%{stroke-dashoffset:0px}100%{stroke-dashoffset:0px}}@keyframes stroke-move2{0%{stroke-dashoffset:900px}5%{stroke-dashoffset:0px}100%{stroke-dashoffset:0px}}@media screen and (max-width: 48rem){.wrap section:last-child{flex-direction:column}.wrap article:last-child{margin-bottom:2.5em}}

.project_list{display:flex;width:100%;height:100%}.project_list ul{display:flex;margin-top:5em;width:100%;height:100%;align-content:flex-start;flex-wrap:wrap;justify-content:center}.project_list li{flex:auto;margin:1em;padding-bottom:1em;min-width:12rem;max-width:16rem;background-color:#fff;box-shadow:1px 2px 5px rgba(0,0,0,0.3)}.project_list li:hover{transform:scale(1.1)}.project_list a{display:block;width:100%;height:100%}.project_list img{width:100%}.project_list .project_txt{padding:1em}.project_list .project_txt h2{margin:0 0 0.5em;text-align:center;font-size:1.3rem}@media screen and (max-width: 48rem){.project_list ul{margin-top:1em}.project_list li:last-child{margin-bottom:3.5em}}

.contact{width:100%;height:100%;display:flex;justify-content:center;align-items:center;flex-direction:column}.contact img{width:5rem}.contact p{text-align-last:left;margin:1em 0}

.not_found{width:100%;height:100%;display:flex;justify-content:center;align-items:center}

